<template>
  <div class="hello">
    <h1>Ripple transaction decoder (using <a href="https://github.com/ripple/ripple-binary-codec">ripple-binary-codec)</a></h1>

    <textarea id="tx-hex-input" rows="5" v-model="hex_tx" v-on:input="updateDecode(hex_tx)" placeholder="Enter hexadecimal transaction here"></textarea>
    <button @click="insertSample()">Insert sample transaction</button>
    <hr>
    <textarea id="tx-json-output" rows="50" v-model="json_tx" disabled></textarea>
  </div>
</template>

<script>

import binary from 'ripple-binary-codec'

export default {
  name: 'RippleTxDecode',
  data () {
    return {
      json_tx: {},
      hex_tx: ''
    }
  },
  methods: {
    updateDecode () {
      try {
        this.json_tx = binary.decode(this.hex_tx)
      } catch (e) {
        this.json_tx = {
          error: e.toString()
        }
      }
      this.json_tx = JSON.stringify(this.json_tx, undefined, 4)
    },
    insertSample () {
      this.hex_tx = '12000322800000002400000017201B00' +
        '86955368400000000000000C732102F8' +
        '9EAEC7667B30F33D0687BBA86C3FE2A0' +
        '8CCA40A9186C5BDE2DAA6FA97A37D874' +
        '473045022100BDE09A1F6670403F341C' +
        '21A77CF35BA47E45CDE974096E1AA5FC' +
        '39811D8269E702203D60291B9A27F1DC' +
        'ABA9CF5DED307B4F23223E0B6F156991' +
        'DB601DFB9C41CE1C770A726970706C65' +
        '2E636F6D81145E7B112523F68D2F5E87' +
        '9DB4EAC51C6698A69304'
      this.updateDecode()
    }
  }
}
</script>



